<script>
import $ from 'jquery'
import "vue-select/dist/vue-select.css"
// import imagesJson from '@/assets/json/images.json' //json

export default {
  name: "",
  props: {},
  data: function() {
    // 
    return {
      src: "", // store
      // 
      optionsList: [
        // '14',
        // 'iMac 27',
        // 'MacBook Pro13 ',
        // '17',
        // ''
      ],
      selected: '', // 
      // url
      search:[],
      // itemsAll: {
      //   '14': 'https://24h.pchome.com.tw/prod/DHAX95-1900AQIVR?q=/S/DHAX9',
      //   'iMac 27': 'https://24h.pchome.com.tw/prod/DYAJFC-A900ASXMF?fq=/S/DYAJFD',
      //   'MacBook Pro13 ': 'https://24h.pchome.com.tw/prod/DYAJCS-1900AQM6T',
      //   '17': 'https://24h.pchome.com.tw/prod/DHAFJ8-A900ABR5Q',
      //   '': 'https://24h.pchome.com.tw/prod/DHAK8A-A900AW63L?fq=/S/DHAK82/'
      // },
      //banner(@ajaxswiper@)
      bannerItem: [],
      //banner1~6
      adItem: [],
      //banner7~12
      adSubItem: [],
      // 
      getPopular: [],
      nav: [
        { 'text': ''},
        { 'text': ''},
        { 'text': ''},
        { 'text': ''},
        { 'text': ''}
      ],
      // 
      getData: [],
      num: '',
      AllData:{},
      currentData: [],
      currentTabName: '', // []
      currentAllData: [],
      selectButton: ''
    }
  },
  watch: {
    //
    'selected' (val) {
      console.log(val,this.search)
      // handler (val) {
        // console.log(this.getSearchItem(),value,this.itemsAll)
        for (let i in this.search){
          if (val === this.search[i].text) window.open(this.search[i].url)
        }
        // this.selected = ''
      // }
    },
    'getData' (items) {
      // Tab
      this.clearTabData()
      if (this.selectButton === '' || this.selectButton === '') {
        this.currentAllData = items
      } else {
        this.initTab(items, this.num)
      }
    }
  },
  computed: {
    //
  },
  methods: {
    toLink (url) {
      window.open(url)
    },
    // 
    // 
    selectGame (name) {
      this.num = name // active class
      // Tab
      this.clearTabData()
      if (name === '') this.currentAllData = this.getData
      else this.initTab(this.getData, name)
      // console.log('this.currentAllData',this.currentAllData)
    },
    initTab (obj, name) {
      // Tab
      this.clearTabData()
      for (var i in obj) {
        if (obj[i].name === name) {
          this.currentData = obj[i].content
        }
      }
      this.currentTabName = name
    },
    // Tab
    clearTabData () {
      this.currentData = []
      this.currentAllData = []
      this.currentTabName = ''
    },
    selectTab(i) {
    // tab i ex.
      this.selectButton = i
      switch (i) {
        case '':
          this.getDataItem('computer')
          this.getPopularItem('computer')
          this.num = ''
          break;
        case '':
          this.getDataItem('phone')
          this.getPopularItem('phone')
          this.num = ''
          break;
        case '':
          this.getDataItem('article')
          this.num = ''
          break;
        case '':
          this.getDataItem('canon')
          this.num = 'Canon'
          break;
        case '':
          this.getDataItem('electrical')
          this.num = ''
          break;
      }
      // 
      if ( i !=='' && i!=='') {
        $('.popular-wrap').css('display','none')
      }else{
        $('.popular-wrap').css('display','block')
      }
    },
    // 
    getSearchItem () {
      let self = this
      this.ajaxSend('search')
        .then(function(res) {
          console.log('search:',res)
          if (res) {
            // console.log('(success):', res)
            if (res.items) self.search = res.items
            for(let i in res.items) {
              self.optionsList.push(res.items[i].text)
            }
          }
        })
        .catch(function (error) {
          console.log('(error):', error)
        })
    },
    // Banner
    getBannerItem () {
      let self = this
      this.ajaxSend('images')
        .then(function(res) {
          if (res) {
            // console.log('Banner(success):', res)
            // banner
            self.bannerItem = res.bannerItem
            // banner1~6
            self.adItem = res.adItem
            // banner1~6
            self.adSubItem = res.adSubItem
          }
        })
        .catch(function (error) {
          console.log('Banner(error):', error)
        })
    },
    // 
    getPopularItem (type) {
      let self = this
      this.ajaxSend(type)
        .then(function(res) {
          if (res) {
            // console.log('(success):', res)
            if (res.computer_popular) self.getPopular = res.computer_popular
            if (res.phone_popular) self.getPopular = res.phone_popular
          }
        })
        .catch(function (error) {
          console.log('(error):', error)
        })
    },
    // 
    getDataItem (type) {
      let self = this
      this.ajaxSend(type)
        .then(function(res) {
          if (res) {
            // console.log('(success):', res)
            if (res.computer_data) self.getData = res.computer_data
            if (res.phone_data) self.getData = res.phone_data
            if (res.article_data) self.getData = res.article_data
            if (res.canon_data) self.getData = res.canon_data
            if (res.electrical_data) self.getData = res.electrical_data
            if (res.forum_data) self.getData = res.forum_data
            if (res.video_data) self.getData = res.video_data
          }
        })
        .catch(function (error) {
          console.log('(error):', error)
        })
    },
    // Ajax
    ajaxSend (fileName) {
      return $.ajax({
        url: `/json/${fileName}.json`,
        type: 'GET',
        dataType: 'json' //json
      });
    }
  },
  //BEGIN--
  beforeCreate: function() {
    //
  },
  created: function() {
    // $data  $el 
    this.src = this.$options.__file ;
  },
  beforeMount: function() {
    //
  },
  mounted: function() {
    // Banner
    this.getBannerItem()
    this.getSearchItem()

    //nav
    $('.nav-item .tab').first().trigger('click');
    $('.nav-item').first().addClass('selected')
    $('.nav-item').click(function(){
      $('.nav-item').removeClass('selected')
      $(this).addClass('selected')
    })

    $('.carousel').carousel({
      interval: 2000
    })

    //gotop
    $(window).scroll(function () {
      if ($(this).scrollTop() > 100) {
        $('.toTop').fadeIn()
      } else {
        $('.toTop').fadeOut()
      }
    })
    $('.toTop').click(function () {
      $('html, body').animate({
        scrollTop: 0
      }, 800)
      return false
    })

    //
    $('.pulse-button').click(function () {
      $(".ad").slideToggle("slow");
      if ($(".arrow-2-x").hasClass('x-ready')) {
        $(".collapse-ad").text("");
        $(".arrow-2-x").removeClass("x-ready");
        $(".iconarrowdown").removeClass("chevron-on").addClass("chevron-down");
      } else {
        $(".collapse-ad").text("");
        $(".arrow-2-x").addClass("x-ready");
        $(".iconarrowdown").removeClass("chevron-down").addClass("chevron-on");
      }
    })
  },
  beforeUpdate: function() {
    // View
  },
  updated: function() {
    // View
  },
  beforeDestroy: function() {
    //
  },
  destroyed: function() {
    //
  }
  //END--
}
</script>

